/* PieChartComponent.css */

/* Container styling */
.pie-chart-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}

/* Button styling */
.pie-chart-container button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.pie-chart-container button:hover {
  background-color: #45a049;
}
