/* BarChartComponent.css */
.bar-chart-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

.bar-chart-header,
.bar-chart-footer {
  width: 100%;
  text-align: center;
  margin: 10px 0;
}

.bar-chart-header img {
  width: 50px;
  height: auto;
  vertical-align: middle;
  margin-right: 10px;
}

.chart-wrapper {
  width: 80%; /* Adjust the width to your needs */
  max-width: 600px; /* Adjust the max-width to your needs */
  margin: 0 auto;
}

