.nav-item {
  cursor: pointer;
}

.nav-item:hover {
  background-color: aqua;
}

.container-main {
  /* width: 80%;
  margin: 0 auto; */
  background-color: #f7f7f9;
  height: 10000px;
}

.tabs {
  display: flex;
  border-bottom: 1px solid #ccc;
  margin-bottom: 20px;
}

.tab-button {
  background-color: #f1f1f1;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.tab-button.active {
  background-color: #ddd;
  border-bottom: 2px solid #007bff;
}

.tab-content {
  display: block;
  position: relative;
}

.tab-content::before {
  content: "";
  /* position: absolute; */
  top: 0;
  bottom: 0;
  left: 75%;
  width: 1px;
  background-color: #ccc;
}

.setting-row {
  /* display: flex; */
  align-items: center;
  /* padding: 10px 0; */
  margin: 0px 70px 5px 10px;
}

.setting-description {
  flex: 1;
  padding-right: 20px;
  font-weight: bold;
}

.setting-control {
  flex: 2;
  padding-left: 20px;
}

hr {
  margin: 10px 0;
  border: 0;
  /* border-top: 1px solid #ccc; */
}

.border-right {
  border-right: 1px solid #ccc;
}

ul.list-unstyled li:hover {
  background-color: #f8f9fa;
}

ul.list-unstyled li {
  transition: background-color 0.3s ease;
}
